import { Role } from "../interfaces/role.interface";

export class UpdateAdminDto{
    public userIdx!: number;
    public department!: string | null;
    public email!: string;
    public phone!: string | null;
    public role!: Role;

    constructor(){
        this.userIdx = -1;
        this.department = '';
        this.email = '';
        this.phone = '';
        this.role = {} as Role;
    }
}