import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { RoleUtility } from '../../../util/role.utility';
import { CreateUserDto } from '../../../dto/create-user.dto';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { RoleName } from '../../../util/enum/role-name.enum';
import { RoleId } from '../../../util/enum/role-id.enum';
import { requiredForm } from '../../../functions/formcontrol/required.validator';
import { email } from '../../../functions/formcontrol/email.validator';
import { DialogCard } from '../../abstracts/dialog-card.abstract';
import { MembershipService } from '../../../membership.service';
import { IBaseResponse } from '../../../../category-manage/interfaces/iBaseResponse.dto';
import { User } from '../../../interfaces/user.interface';
import { GlobalService } from '../../../../../core/services/global.service';
import { Role } from '../../../interfaces/role.interface';

@Component({
  selector: 'app-create-account',
  templateUrl: './create-account.component.html',
  styleUrl: './create-account.component.scss'
})
export class CreateAccountComponent extends DialogCard<CreateAccountComponent> implements OnInit {
  hide = true;
  roleLevel!: string;
  isAdmin!: boolean;
  @Output() event = new EventEmitter<void>();

  constructor(
    private formBuilder: FormBuilder,
    @Inject(MAT_DIALOG_DATA) private data: any,
    dialogRef: MatDialogRef<CreateAccountComponent>,
    private membershipService: MembershipService,
    private globalService: GlobalService,
  ){
    super(dialogRef)
  }

  ngOnInit(): void {
    this.initForm()
    this.setTitle(this.data);
    this.roleLevel = this.data.roleLevel === RoleId.Admin ? '관리자' : '사용자';
    this.isAdmin = this.data.roleLevel === RoleId.Admin? true : false;
  }

  override initForm(){
    this.formGroup = this.formBuilder.group({
      id: ['', [requiredForm('아이디')]],
      name: ['', [requiredForm('이름')]],
      department: ['', []],
      email: ['', [requiredForm('이메일'), email()]],
      phone: ['', []]
    })
    if (this.data.roleLevel === RoleId.Admin){
      this.formGroup.addControl('role', new FormControl('', [requiredForm('권한')]))
    }
  }

  create() {
    if (!this.formGroup.valid){
      this.createErrorMessage(this.formGroup);
      return;
    }
    this.error = '';
    const user = this.createUser();
    this.membershipService.signup<IBaseResponse<User>>(user).subscribe({
      next: result => {
        this.event.next();
        this.close();
        /**회원가입 시 개인 카테고리를 생성하기 위해 api를 추가했습니다. -연정-*/
        this.globalService.dalService.categoryHttp.createPersonal({
          companyCode: 'baseCompany',
          type: 'personal',
          id: result.data.id
        }).subscribe(d => {
          if (!d.isSuccess){
            alert(d.message);
          }
        });
      }
    });
  }

  createUser(){
    const group = this.formGroup;
    const role  = (this.formGroup.get('role') ? 
      this.roleUtility.roleMapByRoleId.get(Number(group.get('role')?.value)) :
      this.roleUtility.roleMapByRoleId.get(RoleId.User) ?? this.roleUtility.roleMapByRoleId.get(RoleId.Admin)) as Role;

    const param = new CreateUserDto();
    param.id = this.formGroup.get('id')?.value;
    param.name = this.formGroup.get('name')?.value;
    param.password = this.formGroup.get('id')?.value;
    param.email = this.formGroup.get('email')?.value
    param.department = this.formGroup.get('department')?.value;
    param.phone = this.formGroup.get('phone')?.value;
    param.role = role;
    param.isPasswordReset = true;

    return param;
  }
}
