import { FieldConfig, RadioButtonConfig, RadioFieldConfig } from "../../interfaces/dynamic-form.interface";
import { ValidatorMap, validatorMapper } from "../mapper/validator.mapper";
import { FieldOption } from "./field.option";

export class RadioFieldOption extends FieldOption {
    override config: RadioFieldConfig = {
        key: "",
        title: "",
        initValue: undefined,
        radios: [],
        validators: [],
    }

    radios(config: RadioButtonConfig[]): this{
        this.config.radios = config;
        return this
    }

    validators(validatorList: Array<keyof ValidatorMap>): this {
        const validators = validatorMapper(this.config.key);

        this.config.validators = validatorList.map(val => validators[val]());
        return this;
    }

    override build(): RadioFieldConfig {
        return this.config;
    }

}