import { Component, ElementRef, Inject, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { requiredForm } from '../../../functions/formcontrol/required.validator';
import { passwordValidator } from '../../../functions/formcontrol/password.validator';
import { passwordMatchValidate } from '../../../functions/formcontrol/password-match.validator';
import { email } from '../../../functions/formcontrol/email.validator';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { DialogCard } from '../../abstracts/dialog-card.abstract';
import { CreateUserDto } from '../../../dto/create-user.dto';
import { RoleId } from '../../../util/enum/role-id.enum';
import { MembershipService } from '../../../membership.service';
import { User } from '../../../interfaces/user.interface';
import { Router } from '@angular/router';
import { IBaseResponse } from '../../../../../shared/interfaces/base-response.interface';
import { HttpErrorResponse } from '@angular/common/http';
import { GlobalService } from '../../../../../core/services/global.service';
import { Role } from '../../../interfaces/role.interface';

@Component({
  selector: 'app-signup-stepper',
  templateUrl: './signup-stepper.component.html',
  styleUrl: './signup-stepper.component.scss'
})
export class SignupStepperComponent extends DialogCard<SignupStepperComponent> implements OnInit {

  firstFormGroup!: FormGroup;
  secondFormGroup!: FormGroup;

  firstStepError!: string;
  secondStepError!: string;

  hide = true;
  hideConfirm = true;

  submitError!: string;

  @ViewChild('confirmPasswordInput', {static: true})confirmPasswordInput!: ElementRef;

  constructor(
    private formBuilder: FormBuilder,
    @Inject(MAT_DIALOG_DATA) private data: any,
    dialogRef: MatDialogRef<SignupStepperComponent>,
    private router: Router,
    private globalService: GlobalService
  ){
    super(dialogRef)
  }

  ngOnInit(): void {
    this.initForm();
    this.setTitle(this.data);
  }

  override initForm(){
    this.firstFormGroup = this.formBuilder.group({
      id: ['', [requiredForm('아이디')]],
      password: ['', [requiredForm('비밀번호'), passwordValidator(this.confirmPasswordInput)]],
      confirmPassword: ['', [requiredForm('비밀번호 확인'), passwordMatchValidate()]]
    })
    this.secondFormGroup = this.formBuilder.group({
      name: ['', [requiredForm('이름')]],
      department: ['', []],
      email: ['', [requiredForm('이메일'), email()]],
      phone: ['', []]
    }) 
    this.formGroup = this.formBuilder.group({
      firstFormGroup: this.firstFormGroup,
      secondFormGroup: this.secondFormGroup
    })
  }

  signup(){
    if(!this.firstFormGroup.valid){
      this.createErrorMessage(this.firstFormGroup)
      return;
    }
    this.error = '';
    if (!this.secondFormGroup.valid) {
      this.createErrorMessage(this.secondFormGroup)
      return;
    }
    this.error = '';
    const user = this.createParam();
    this.globalService.membershipService.signup<IBaseResponse<User>>(user).subscribe({
      next: result => {
        /**회원가입 시 개인 카테고리를 생성하기 위해 api를 추가했습니다. -연정-*/
        this.globalService.dalService.categoryHttp.createPersonal({
          companyCode: 'baseCompany',
          type: 'personal',
          id: result.data.id
        }).subscribe(d => {
          if (!d.isSuccess){
            alert(d.message);
          }
        });

        this.login(result.data);
      },
      error: (e:HttpErrorResponse) => {
        this.submitError = e.error.data.message;
      }
    })
  }

  login(user: User){
    this.globalService.currentUser.setUser(user);
    this.success();
  }
  
  success(){
    this.dialogRef.close();
    this.router.navigate(['/chatbot']);
  }

  createParam(){
    const param = new CreateUserDto();
    param.id = this.firstFormGroup.get('id')?.value;
    param.name = this.secondFormGroup.get('name')?.value;
    param.password = this.firstFormGroup.get('password')?.value;
    param.email = this.secondFormGroup.get('email')?.value
    param.department = this.secondFormGroup.get('department')?.value;
    param.phone = this.secondFormGroup.get('phone')?.value;
    param.role = this.roleUtility.roleMapByRoleId.get(RoleId.User) as Role;
    param.isPasswordReset = false;
    return param;
  }

  checkStep(formGroup: FormGroup){
    if(!formGroup.valid){
      this.createErrorMessage(formGroup);
      console.log(this.error);
      
      return;
    }
    this.error = '';
  }

  

}
