import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './components/login/login.component';
import { AdminControlComponent } from './components/admin-control/admin-control.component';
import { UserControlComponent } from './components/user-control/user-control.component';
import { roleGuard } from '../../core/guards/roleguard.guard';
import { canActivateLogin } from '../../core/guards/authguard.guard';
import { RoleName } from './util/enum/role-name.enum';

export const routes: Routes = [
  {
    path: '', 
    redirectTo: 'login',
    pathMatch: 'full'
  },
  {
    path: 'login',
    component: LoginComponent,
    canActivate: [canActivateLogin]
  },
  {
    path: 'admin',
    component: AdminControlComponent,
    canActivate: [roleGuard],
    data: {
      requiredRoles: [RoleName.SuperAdmin]
    }
  },
  {
    path: 'user',
    component: UserControlComponent,
    canActivate: [roleGuard],
    data: {
      requiredRoles: [RoleName.Admin, RoleName.SuperAdmin]
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MembershipRoutingModule { }
