import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MembershipService } from '../../../membership.service';
import { DialogCard } from '../../abstracts/dialog-card.abstract';
import { ResetPasswordDto } from '../../../dto/reset-password.dto';
import { IBaseResponse } from '../../../../category-manage/interfaces/iBaseResponse.dto';
import { User } from '../../../interfaces/user.interface';

@Component({
  selector: 'app-check-account',
  templateUrl: './check-account.component.html',
  styleUrl: './check-account.component.scss'
})
export class CheckAccount extends DialogCard<CheckAccount> implements OnInit {
  hide = true;

  constructor(
    private dialog: MatDialogRef<CheckAccount>,
    private formBuilder: FormBuilder,
    private membershipService: MembershipService,
    @Inject(MAT_DIALOG_DATA) private data: any
  ) { 
    super(dialog)
  }

  ngOnInit(): void {
    this.initForm()
    this.setTitle(this.data);
  }

  override initForm(){
    this.formGroup = this.formBuilder.group({
      id: ['', [Validators.required]],
      email: ['', [Validators.required, Validators.email]]
    })
  }

  reset(){
    const param = this.createParam();
    this.membershipService.resetPassword<IBaseResponse<boolean>>(param).subscribe({
      next: res => {
        console.log('결과',res);
        this.close();
      },
      error: e => console.log(e)
      
    })
  }

  createParam(){
    const param: ResetPasswordDto = new ResetPasswordDto()
    param.id = this.formGroup.get('id')?.value;
    param.email = this.formGroup.get('email')?.value;
    return param;
  }
}
