import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { CheckAccount } from '../dialogs/check-account/check-account.component';
import { MembershipService } from '../../membership.service';
import { requiredForm } from '../../functions/formcontrol/required.validator';
import { Router } from '@angular/router';
import { SignupStepperComponent } from '../dialogs/signup-stepper/signup-stepper.component';
import { Subscription } from 'rxjs';
import { User } from '../../interfaces/user.interface';
import { ChangePasswordComponent } from '../dialogs/change-password/change-password.component';
import { LoginDto } from '../../dto/login.dto';
import { IBaseResponse } from '../../../../shared/interfaces/base-response.interface';
import { DialogService } from '../dialogs/dialog-layout/dialog.service';
import { GlobalService } from '../../../../core/services/global.service';
import { unsubscribe } from '../../functions/decorators/unsubscribe.decorator';

@unsubscribe()
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrl: './login.component.scss',
})
export class LoginComponent implements OnInit, OnDestroy {
  hide = true;
  error!: any;

  loginForm!: FormGroup;
  subscription!: Subscription;

  testId = this.membershipServie.userId;

  constructor(
    private router: Router,
    private dialog: MatDialog,
    public membershipServie: MembershipService,
    private dialogService: DialogService,
    private formBuilder: FormBuilder,
    private globalService: GlobalService
  ){}

  ngOnInit(): void {
    this.initForm();
    console.log('로그인');

    this.listen();
    // this.checkLogin();
  }

  // 1. 로그인 정보 확인
  // 2. 있으면 초기화 확인
  // 3. 초기화 아니면 로그인

  // 2. 없으면 로그인 페이지

  // 3. 초기화라면 초기화 다이얼로그
  // 4. 로그인 불가

  initForm(){
    this.loginForm = this.formBuilder.group({
      id: ['', [requiredForm('id')]],
      password: ['', [requiredForm('password')]]
    });
  }

  login(){
    if(!this.validate()) return;
    this.error = null;
    const param = this.createParam();
    this.globalService.membershipService.login<IBaseResponse<User>>(param).subscribe({
      next: res => {
        console.log('login^^', res);
        this.checkReset(res.data);
        // this.checkReset(res);
      },
      error: e => {
        console.log(e);
        this.error = e.error.data.message;
      }
    })
    
  }

  checkReset(user: User) {
    user.isPasswordReset ?
      this.changePassword(user) :
      this.success(user)
  }

  validate(){
    if (!this.loginForm.valid) {
      this.createErrorMessage();
      return false;
    }
    return true;
  }

  createParam(){
    const param = new LoginDto();
    param.id = this.loginForm.get('id')?.value;
    param.password = this.loginForm.get('password')?.value;
    return param;
  }

  createErrorMessage(){
    for(const [key, value] of Object.entries(this.loginForm.controls)){
      if(value.invalid){
        return this.error = value.errors!['message'];
      }
    }
  }

  changePassword(user: User){
    const dialog = this.dialog.open(ChangePasswordComponent, {
      width: '40%',
      minWidth: '500px',
      maxWidth: '550px',
      disableClose: true,
      data: {
        userSeq: user.userIdx,
        title: '비밀번호 변경'
      }
    })

    dialog.afterClosed().subscribe({
      next: result => {
        console.log('변경후', result);
        
        if(result) {
          this.success(user);
        };
      }
    })
  }

  success(user: User){
    this.globalService.currentUser.setUser(user);
    this.membershipServie.setUser(user);
    this.router.navigate(['/main']);
  }

  checkAccount(){
    const dialogRef = this.dialog.open(CheckAccount, {
      width: '40%',
      minWidth: '350px',
      maxWidth: '500px',
      data: {
        title: '비밀번호 초기화'
      }
    });
  }

  signUp() {
    // const dialog = this.dialog.open(SignupDialogComponent, {
    const dialog = this.dialog.open(SignupStepperComponent, {
      width: '35%',
      minWidth: '350px',
      maxWidth: '500px',
      data: {
        title: '회원가입'
      }
    });
  }

  ngOnDestroy(): void {
    console.log('ngOnDestroy',this.testId);
  }

  listen(){
    this.subscription = this.dialogService.eventListener$.subscribe({
      next: event => {
        console.log('최초 리스너 ', event);
        (this.dialogService as any)[event.event](event.param);
      }
    })
  }

  openDialog(){
    this.dialogService.eventListener$.next({
      event: 'open'
    });
  }

}
