import { EventEmitter } from "@angular/core";
import { FormControl, ValidatorFn } from "@angular/forms";
import { RadioFieldConfig } from "../../interfaces/dynamic-form.interface";
import { DynamicField } from "./dynamic-field.abstract";
import { RadioFieldOption } from "../../util/generator/radio.field.option";
import { RadioFieldData } from "../../interfaces/dynamic-form-option.interface";

/**
 * 라디오 버튼 데이터 바인딩 추상 클래스
 */
export abstract class AbstractRadioFeild extends DynamicField {
    abstract event: EventEmitter<FormControl>;
    config!: RadioFieldConfig;
    control!: FormControl;

    init(initData: RadioFieldData){
        const { key, title, initValue, radios, validators } = initData;
        const config = this.generator.generate(RadioFieldOption)
            .key(key).title(title).initValue(initValue).radios(radios).validators(validators).build();
        this.config = config;
        this.controlInit(config.validators);
    }
    

    controlInit(validators: ValidatorFn[]){
        this.control = new FormControl(this.config.initValue, [...validators]);
        this.event.next(this.control);
    }
}