import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MembershipService } from '../../../membership.service';
import { User } from '../../../interfaces/user.interface';
import { passwordValidator } from '../../../functions/formcontrol/password.validator';
import { passwordMatchValidate } from '../../../functions/formcontrol/password-match.validator';
import { CreateUserDto } from '../../../dto/create-user.dto';
import { RoleUtility } from '../../../util/role.utility';
import { RoleId } from '../../../util/enum/role-id.enum';
import { requiredForm } from '../../../functions/formcontrol/required.validator';
import { email } from '../../../functions/formcontrol/email.validator';
import { Role } from '../../../interfaces/role.interface';

@Component({
  selector: 'app-signup-dialog',
  templateUrl: './signup-dialog.component.html',
  styleUrl: './signup-dialog.component.scss'
})
export class SignupDialogComponent implements OnInit {
  registerForm!: FormGroup;
  hide: boolean = true;
  isLogin!: boolean;
  error: string = '';
  roleUtil!: RoleUtility;

  @Output() switchLoginEmitter = new EventEmitter<void>(); 

  constructor(
    public dialogRef: MatDialogRef<SignupDialogComponent>,
    private formBuilder: FormBuilder,
    private membershipService: MembershipService,
    @Inject(MAT_DIALOG_DATA) public data: any
    // private store: Store<AppState>
  ) { }

  ngOnInit() {
    this.initForm();
    this.roleUtil = new RoleUtility();
  }

  initForm(){
    this.registerForm = this.formBuilder.group({
      id: ['', [requiredForm('아이디')]],
      name: ['', [requiredForm('이름')]],
      department: [''],
      email: ['', [requiredForm('이메일'), email()]],
      phone: [],
      password: ['', [requiredForm('비밀번호'), passwordValidator()]],
      confirmPassword: ['', passwordMatchValidate()],
    });
  }

  createUser(){
    const formGroup = this.registerForm.controls
    console.log('폼그룹',formGroup);

    const param = new CreateUserDto();
    param.id = this.registerForm.get('id')?.value;
    param.name = this.registerForm.get('name')?.value;
    param.password = this.registerForm.get('password')?.value;
    param.email = this.registerForm.get('email')?.value
    param.department = this.registerForm.get('department')?.value;
    param.phone = this.registerForm.get('phone')?.value;
    param.role = this.roleUtil.roleMapByRoleId.get(RoleId.User) as Role;
    param.isPasswordReset = false;

    this.registerForm.reset()
    return param;
  }

  onSubmit(){
    if(!this.registerForm.valid){
      console.log(this.registerForm);
      this.createErrorMessage();
      return;
    }
    this.error = '';
    const user = this.createUser();
    // if(this.registerForm.valid){
    //   const user = this.createUser();
    //   this.membershipService.signup(user)
    //     .subscribe({
    //       next: (result: any) => {
    //         console.log(result);
    //         this.membershipService.user.next(result);
    //         this.dialogRef.close();
    //       },
    //       error: err => {
    //         this.error = err.error.message;
    //         console.log('회원가입 실패', err);
    //       }
    //     })
    // }
  }

  createErrorMessage() {
    for (const [key, value] of Object.entries(this.registerForm.controls)) {
      if (value.invalid) {
        return this.error = value.errors!['message'];
      }
    }
  }

  cancel(){
    this.dialogRef.close();
  }

  switchLogin(){
    this.switchLoginEmitter.next();
    this.dialogRef.close();
  }
}
