import { Component, OnInit } from '@angular/core';
import * as dummy from '../../dummy.json';
import { MatDialog } from '@angular/material/dialog';
import { CreateAccountComponent } from '../dialogs/create-account/create-account.component';
import { RoleId } from '../../util/enum/role-id.enum';
import { ManageAccountComponent } from '../dialogs/manage-account/manage-account.component';
import { ConfirmComponent } from '../dialogs/confirm/confirm.component';
import { MembershipService } from '../../membership.service';
import { IBaseResponse } from '../../../category-manage/interfaces/iBaseResponse.dto';
import { User } from '../../interfaces/user.interface';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-admin-control',
  templateUrl: './admin-control.component.html',
  styleUrl: './admin-control.component.scss'
})
export class AdminControlComponent implements OnInit {
  dataSource: any;
  displayDataSource: any;
  displayedColumns = dummy.displayedColumns;
  roleLevel!: RoleId;

  constructor(
    private dialog: MatDialog,
    private membershipService: MembershipService
  ){}

  ngOnInit(): void {
    this.roleLevel = RoleId.Admin;
    this.getUserLlist();

  }

  getUserLlist(){
    this.membershipService.getAdminList<IBaseResponse<User[]>>().subscribe({
      next: res => {
        this.dataSource = res.data;
        this.refineData();
      }
    })
  }

  refineData(){
    this.displayDataSource = this.dataSource.map((d: User) => {
      return {
        ...d,
        role: d.role!.roleName
      }
    })
  }

  updateAccount(index: number) {
    const dialog = this.dialog.open(ManageAccountComponent, {
      width: '80%',
      minWidth: '500px',
      maxWidth: '600px',
      data: {
        title: '계정관리',
        user: this.dataSource[index],
        roleLevel: this.roleLevel
      }
    });

    dialog.componentInstance.event.subscribe(() => {
      this.getUserLlist();
    })
  }

  createUser(){
    
    const dialog = this.dialog.open(CreateAccountComponent, {
      width: '80%',
      minWidth: '500px',
      maxWidth: '600px',
      data: {
        title: '관리자 등록',
        roleLevel: this.roleLevel
      }
    });

    dialog.componentInstance.event.subscribe(() => this.getUserLlist());

  }

  confirmRemove(index: number){
    const dialog = this.dialog.open(ConfirmComponent, {
      width: '40%',
      minWidth: '350px',
      maxWidth: '500px',
      data: {
        title: '계정 삭제',
        contentMessage: ['선택하신 계정을 제거하시겠습니까?']
      }
    })

    dialog.componentInstance.event.subscribe({
      next: () => {
        this.removeAccount(index);
      }
    })
  }

  removeAccount(index: number){
    const userIdx = this.displayDataSource[index].userIdx;
    this.membershipService.deleteUser(userIdx).subscribe({
      next: result => {
        this.getUserLlist();
      },
      error: (e:HttpErrorResponse) => {
        console.log(e);
      }
    });
    
  }
}
