import { Component, ElementRef, Inject, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { passwordValidator } from '../../../functions/formcontrol/password.validator';
import { requiredForm } from '../../../functions/formcontrol/required.validator';
import { passwordMatchValidate } from '../../../functions/formcontrol/password-match.validator';
import { DialogCard } from '../../abstracts/dialog-card.abstract';
import { MembershipService } from '../../../membership.service';
import { UpdatePassowdDto } from '../../../dto/update-password.dto';
import { IBaseResponse } from '../../../../category-manage/interfaces/iBaseResponse.dto';

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrl: './change-password.component.scss'
})
export class ChangePasswordComponent extends DialogCard<any> implements OnInit {

  // passwordForm!: FormGroup
  isSuccess = false;
  hide = true;
  hideConfirm = true;

  @ViewChild('confirmPasswordInput', {static: true})confirmPasswordInput!: ElementRef;

  constructor(
    dialogRef: MatDialogRef<ChangePasswordComponent>,
    @Inject(MAT_DIALOG_DATA) private data: any,
    private formBuilder: FormBuilder,
    private membershipService: MembershipService
  ){
    super(dialogRef);
  }

  ngOnInit(): void {
    this.initForm();
    this.setTitle(this.data);
  }

  override initForm(){
    this.formGroup = this.formBuilder.group({
      password: ['', [requiredForm('비밀번호'), passwordValidator(this.confirmPasswordInput)]] ,
      confirmPassword: ['', [passwordMatchValidate()]] 
    })
  }

  update(){
    const param = this.createParam();
    this.membershipService.updatePassword<IBaseResponse<boolean>>(param).subscribe({
      next: res => {
        console.log(res);
        this.success();
      }
    })
  }

  success(){
    this.close(true);
  }

  createParam(){
    const param: UpdatePassowdDto = new UpdatePassowdDto();
    param.userIdx = this.data.userSeq;
    param.password = this.formGroup.get('password')?.value;
    return param;
  }
}
