import { Component, EventEmitter, Inject, Output } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { RoleUtility } from '../../../util/role.utility';
import { RoleId } from '../../../util/enum/role-id.enum';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { CreateUserDto } from '../../../dto/create-user.dto';
import { UpdateUserDto } from '../../../dto/update-user.dto';
import { passwordValidator } from '../../../functions/formcontrol/password.validator';
import { passwordMatchValidate } from '../../../functions/formcontrol/password-match.validator';
import { ValidatePasswordComponent } from '../validate-password/validate-password.component';
import { User } from '../../../interfaces/user.interface';
import { requiredForm } from '../../../functions/formcontrol/required.validator';
import { email } from '../../../functions/formcontrol/email.validator';
import { ConfirmComponent } from '../confirm/confirm.component';
import { DialogCard } from '../../abstracts/dialog-card.abstract';
import { UpdateAdminDto } from '../../../dto/update-admin.dto';
import { MembershipService } from '../../../membership.service';
import { IBaseResponse } from '../../../../category-manage/interfaces/iBaseResponse.dto';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-manage-account',
  templateUrl: './manage-account.component.html',
  styleUrl: './manage-account.component.scss'
})
export class ManageAccountComponent extends DialogCard<ManageAccountComponent> {
  user!: User;
  isValid: boolean = false;
  hide = true;
  isAdmin!: boolean;

  @Output() event: EventEmitter<void> = new EventEmitter();

  constructor(
    dialogRef: MatDialogRef<ManageAccountComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private dialog: MatDialog,
    private formBuilder: FormBuilder,
    private membershipService: MembershipService
  ) { 
    super(dialogRef)
  }

  ngOnInit(): void {
    this.user = this.data.user
    this.isAdmin = this.data.roleLevel === RoleId.Admin ? true : false;
    this.initForm();
    this.setTitle(this.data);
  }

  override initForm() {
    this.formGroup = this.formBuilder.group({
      department: [this.user.department],
      email: [this.user.email, [requiredForm('이메일'), email()]],
      phone: [this.user.phone,]
    })
    if (this.data.roleLevel === RoleId.Admin) {
      this.formGroup.addControl('role', new FormControl(this.user.role?.roleId, [requiredForm('권한')]))
    }
  }

  confirm(){
    const dialog = this.dialog.open(ConfirmComponent, {
      width: '40%',
      minWidth: '350px',
      maxWidth: '500px',
      data: {
        title: '비밀번호 초기화',
        contentMessage: ['비밀번호를 초기화 하시겠습니까?','초기화 시 최초 비밀번호는 아이디와 동일합니다.']
      }
    })

    dialog.componentInstance.event.subscribe({
      next: () => {
        this.resetPassword();
      }
    })
  }

  resetPassword(){
    console.log('암호 초기화');
    this.membershipService.resetPassword<IBaseResponse<boolean>>({
      id: this.user.id,
      email: this.user.email!
    }).subscribe({
      next: result => {
        console.log('비밀번호 초기화 \n', result);
      },
      error: (e:HttpErrorResponse) => {
        this.error = e.error.data.message;
      }
    })
  }

  update() {
    if (!this.formGroup.valid) {
      console.log(this.formGroup);
      this.createErrorMessage(this.formGroup);
      return;
    }
    this.error = '';
    const manageParam = this.createParam();
    console.log('파람, ', manageParam);
    this.membershipService.updateAdmin<IBaseResponse<boolean>>(manageParam).subscribe({
      next: result => {
        console.log('수정 결과, \n', result);
        this.success();
      },
      error: (e: HttpErrorResponse) => {
        console.log('수정 실패, \n');
        this.error = e.error.data.message;
      }
    })
  }


  createParam() {
    const group = this.formGroup
    const param = new UpdateAdminDto();
    param.userIdx = this.user.userIdx!;
    param.email = group.get('email')?.value;
    param.department = group.get('department')?.value;
    param.phone = group.get('phone')?.value;
    param.role = this.formGroup.get('role') ?
      this.roleUtility.roleMapByRoleId.get(Number(group.get('role')?.value))! :
      this.roleUtility.roleMapByRoleId.get(RoleId.User)!;
    return param;
  }

  success(){
    this.event.next();
    this.close();
  }

}
